'use strict';

export function transpose(a) {
	const w = a.length;
	const h = a[0].length;
	let b = new Array(h);
	for (let y=0; y<h; y++) {
		b[y] = new Array(w);
		for (let x=0; x<w; x++) {
			b[y][x] = a[w-1-x][y];
		}
	}
	return b;
}

export function mirror(a) {
	const w = a[0].length;
	const h = a.length;
	let b = new Array(h);
	for (let y=0; y<h; y++) {
		let n = h-1-y;
		b[n] = new Array(w);
		for (let x=0; x<w; x++) {
			b[n][w-1-x] = a[y][x];
		}
	}
	return b;
}

export function mirrorTranspose(a) {
	const w = a.length;
	const h = a[0].length;
	let b = new Array(h);
	for (let y=0; y<h; y++) {
		b[y] = new Array(w);
		for (let x=0; x<w; x++) {
			b[y][x] = a[x][h-1-y];
		}
	}
	return b;
}

export function hflip(a) {
	const h = a.length;
	let b = new Array(h);
	for (let y=0; y<h; y++) {
		let w = a[y].length;
		b[y] = new Array(w);
		for (let x=0; x<w; x++) {
			let n = w-1-x;
			b[y][n] = a[y][x];
		}
	}
	return b;
}

export function vflip(a) {
	const h = a.length;
	let b = new Array(h);
	for (let y=0; y<h; y++) {
		let w = a[y].length;
		let n = h-1-y;
		b[n] = new Array(w);
		for (let x=0; x<w; x++) {
			b[n][x] = a[y][x];
		}
	}
	return b;
}


export function rotate(a, deg) {
	if (deg % 90 !== 0) {
		throw new Error(
			'Invalid input; degrees must be a multiple of 90'
		);
	}
	const d = ((deg % 360) + 360) % 360;
	if (d === 90) {
		return transpose(a);
	} else if (d === 180) {
		return mirror(a);
	} else if (d === 270) {
		return mirrorTranspose(a);
	}
	return a;
}

export function rotate90CW(a) {
	return transpose(a);
}

export function rotate180CW(a) {
	return mirror(a);
}

export function rotate270CW(a) {
	return mirrorTranspose(a);
}
